/**
 * Footer styles
 */

.footer-widgets {

    text-align: left;
    background-color: $hero-color;
    color: $color-light;
    margin: 0;
    padding: 1.5em 1em 1em;
    @include linear-gradient(to right);
    position: relative;

    .widget .widget-title {
      color: $color-light;
    }

    a {
      font-weight: 300;
      @extend .all-links;
      color: $color-light;
    }

    @media #{$tablet__breakpoint} {

        display: flex;
        flex-wrap: wrap;
        padding: 2.25em 3.375em;

        .widget {
           
            width: auto;
            padding: 0.5em;
            
            &:first-child {
               flex: 1 1 40rem;
            }
            
            &:nth-child(2) {
               flex: 1 1 0px;
               flex-basis: auto;
            }
            
            &:nth-child(3),
            &:last-child {
               flex: 1 1 auto;
            }

            &:last-child {
               margin-left: 1em;
            }
        }



    }

    @media #{$desktop__breakpoint} {

        padding-left: 5.063em;
        padding-right: 5.063em;



    }

}

.footer-widgets #custom_html-6 {
   span {
      display: inline-block;
      width: 100%;
      line-height: 1;
   }
}

/**
 * NOTE: This is the legal footer
 */
.site-footer {

    display: flex;
    justify-content: space-between;
    padding: 0.5em 0;

    nav {

        a {

            text-decoration: none;

        }

        .menu {

            list-style: none;
            padding: 0;
            margin: 0;

            li {

                display: inline-block;

                a {
                    padding: 0 0.25em;
                }
            }

        }
    }

    > div,
    > nav {
        display: flex;
        align-items: center;
    }

    #copyright,
    #legal-menu > ul {

        font-size: 75%;

    }

    #social-media-menu > ul {

        font-size: 125%;

        a:link,
        a:visited {
          color: $hero-color;
        }

    }
/*    #legal-menu {
        li:last-child {
            margin-left: 0.65em;
        }
    }    */

    @media #{$mobile-only__breakpoint} {
        justify-content: center;
        flex-wrap: wrap;
        #social-media-menu {
            order: 1;
            flex-basis: 100%;
            justify-content: center;
            font-size: 150%;
        }
        #legal-menu {
            order: 2;
            flex-basis: 100%;
            justify-content: center;
            margin: 0.5em 0;
            a {
                padding: 0.5em;
            }
        }
        #copyright {
            order: 3;
            margin: 0.5em 0;
        }
    }

    @media #{$tablet__breakpoint} {
        justify-content: space-around;
    }
}
